import request from "@/api/request";

function saveAction(data) {
    return request({
        method: 'POST',
        url: '/address/saveAction',
        data
    })
}

function detailAction(data) {
    return request({
        method: 'get',
        url: '/address/detailAction',
        data
    })
}
export {
    saveAction,
    detailAction
}