import request from "@/api/request";

// 订单确认界面数据
function detailAction1(data) {
    return request({
        method: 'GET',
        url: '/order/detailAction',
        data
    })
}

function detailAction(data) {
    return request({
        method: 'GET',
        url: 'detailAction',
        data
    })
}


export {
    detailAction,
    detailAction1
}