<template>
    <div>
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <div class="site">
            <!-- 背景图片 -->
            <div class="sitebg">
            </div>
            <!-- 选择s收货地址 -->
            <div class="site-select" @click="goAddress">
                请选择收获地址
            </div>
            <!-- 有默认地址时显示默认地址 -->
            <div class="address" v-if="show" @click="goAddress">
                <div class="address-item">
                    <h3>{{list.address.name}}
                        <span :class="[list.address.is_default ==1?'act':'']">默认</span>
                    </h3>
                    <div class="info">
                        <p>{{list.address.mobile}}</p>
                        <p>{{list.address.address}}</p>
                    </div>
                    <i @click="goAddsite(list.address.id)">
                        <van-icon name="records" size="33" /></i>
                </div>
            </div>
            <!-- 没有展示选中地址 -->
            <div class="address" v-if="isshow" @click="goAddress">
                <div class="address-item">
                    <h3>{{address.name}}
                        <span :class="[address.is_default ==1?'act':'']">默认</span>
                    </h3>
                    <div class="info">
                        <p>{{address.mobile}}</p>
                        <p>{{address.address}}</p>
                    </div>
                    <i @click="goAddsite(address.id)">
                        <van-icon name="records" size="33" /></i>
                </div>
            </div>
        </div>
        <!-- 订单信息 -->
        <div class="info">
            <div class="info-item">
                <h4>商品合计</h4>
                <span>￥{{price/100}}</span>
            </div>
            <div class="info-item">
                <h4>运费合计</h4>
                <span>免运费</span>
            </div>
            <div class="info-item">
                <h4>优惠卷</h4>
                <span>暂无</span>
            </div>
        </div>
        <!-- 显示商品详情 -->
        <div class="content">
            <div class="content-item" @click="goInfo(item.goods_id)" v-for="item in list.goodsList" :key="item.id">
                <img :src="item.list_pic_url" alt="">
                <p>{{item.goods_name}}
                    <span>￥{{item.retail_price}}</span>
                </p>
                <h4>X{{item.number}}</h4>
            </div>
        </div>
        <div class="foot">
            <p>实付:￥{{price/100}}</p>
            <!-- 支付按键 -->
            <div class="pay">
                支付
            </div>
        </div>
    </div>
</template>

<script>
    // 引入接口
    import {
        detailAction1,

    } from '@/api/cart/order/index.js'
    import {
        getListAction
    } from '@/api/my/address/index.js'
    import {

        detailAction,
    } from "@/api/cart/order/address/index.js"
    export default {
        data() {
            return {
                list: [],
                address: {},
                addressList: [{
                    id: ''
                }],
                show: false,
                isshow: false
            };
        },
        computed: {
            // 获取传递的价格
            price() {
                return this.$route.query.allPrise;
            }
        },
        created() {

        },
        mounted() {
            // 获取订单信息
            detailAction1({
                openId: localStorage.getItem('openId'),
            }).then(res => {
                this.list = res.data
                // 判断是否有默认地址
                if (res.data.address.is_default == 1) {
                    // 有默认地址展示默认地址
                    this.show = true
                } else {
                    detailAction({
                        id: this.$store.state.address
                    }).then(res => {
                        this.address = res.data.data
                    })
                    this.isshow = true
                }

            });
            // 获取全部地址列表
            getListAction({
                openId: localStorage.getItem('openId')
            }).then(res => {
                if (res.data.data.is_default == 1) {
                    return
                } else {
                    this.adderessList = res.data.data
                }
            })
        },
        methods: {
            // 返回事件
            onClickLeft() {
                history.back();
            },
            //    跳转商品详情页面
            goInfo(id) {
                this.$router.push(`/category/goods/info?id=${id}`)
            },
            // 根据条件跳转页面
            goAddress() {
                // 地址列表为空则跳转新建地址
                if (this.adderessList[0]) {
                    this.$router.push(`/my/address`)
                } else {
                    this.$router.push(`/cart/order/address`)
                }

            }

        },

    };
</script>

<style scoped lang="scss">
    .ss {
        position: fixed;
        top: 400px;
        left: 20px;
    }

    .site {
        .sitebg {
            width: 100%;
            height: 5px;
            background-image: url("../../../assets/images/address-bg-bd.png");
        }

        .site-select {
            width: 100%;
            padding: 30px 0px;
            text-align: center;
            background-color: #fff;
            margin-bottom: 10px;
        }
    }

    .info {
        background-color: #fff;
        padding: 0px 15px;

        .info-item {
            padding: 15px 0px;
            border-bottom: 1px solid #ccc;

            h4 {
                display: inline-block;
            }

            span {
                float: right;
            }
        }
    }

    .content {
        .content-item {
            display: flex;
            padding: 20px;
            background-color: #fff;
            margin-top: 10px;
            justify-content: space-between;

            img {
                width: 70px;
                height: 70px;
            }

            p {
                text-align: center;
                display: flex;
                align-items: center;
            }

            h4 {
                text-align: center;
                display: flex;
                align-items: center;
                font-weight: normal;
            }
        }
    }

    .foot {
        width: 100%;
        height: 55px;
        position: absolute;
        bottom: 0;
        left: 0;

        p {
            float: left;
            padding: 0px 10px;
            width: 100px;
            height: 55px;
            line-height: 55px;
        }

        .pay {
            float: right;
            width: 110px;
            height: 55px;
            background-color: #b4282d;
            text-align: center;
            line-height: 55px;
            color: #fff;
            font-size: 14px;
        }
    }

    .address {
        width: 100%;
        position: absolute;
        top: 50px;
        left: 0;

        .address-item {
            position: relative;
            display: flex;
            background-color: #fff;
            justify-content: space-between;
            padding: 20px 10px;
            border-bottom: 1px solid #ccc;

            h3 {
                width: 20%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                text-align: center;
            }

            .info {
                width: 60%;

                p {
                    margin: 5px 0px;
                }
            }

            i {
                display: flex;
                align-items: center;
            }

            span {
                width: 35px;
                height: 20px;
                border: 1px solid #f00;
                text-align: center;
                line-height: 20px;
                color: #f00;
                display: none;
                margin: 0px auto;
                margin-top: 5px;
            }
        }
    }
</style>